const TypeIcon = ({text}) => {
                                                                                                                                       // activity  0 good 3 -- low priority 1- remind 2 critical 
    return (
        <div className='flex items-center flex items-center text-14px text-cblock font-[600]'>
            <div className={`mr-[10px]    ${text == 0 ? "text-cblock" : text == 1 ? "text-Warning" : text == 2 ? "text-onBackWarring" : "text-[#4285F4]"}`}>{text == 0 ? "Good" : text == 1 ? "Warning" : text == 2 ? "Critical" : "Low Priority"}</div>
            {
                text == 0 ? (<svg xmlns="http://www.w3.org/2000/svg" width="12" height="14" viewBox="0 0 12 14" fill="none">
                    <path d="M-3.02841e-07 7.00002L12 0.071815L12 13.9282L-3.02841e-07 7.00002Z" fill="#6FCF97" />
                </svg>) : text == 1 ? (<svg xmlns="http://www.w3.org/2000/svg" width="12" height="14" viewBox="0 0 12 14" fill="none">
                    <path d="M12 6.99998L-6.05683e-07 13.9282L0 0.0717768L12 6.99998Z" fill="#FAB515" />
                </svg>) : text == 2 ? (<svg xmlns="http://www.w3.org/2000/svg" width="14" height="13" viewBox="0 0 14 13" fill="none">
                    <path d="M6.9282 0.5L13.8564 12.5H0L6.9282 0.5Z" fill="#FA4B5E" />
                </svg>) : (<svg xmlns="http://www.w3.org/2000/svg" width="14" height="13" viewBox="0 0 14 13" fill="none">
                    <path d="M7.0718 12.5L0.143594 0.499999L14 0.5L7.0718 12.5Z" fill="#4285F4" />
                </svg>)
            }
        </div>
    )
}
export default TypeIcon;